.about {
  padding-top: 80px;
}

.company-info {
  padding: 5rem 2rem;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 4rem;
  max-width: 1200px;
  margin: 0 auto;
  align-items: center;
}

.info-content h2 {
  font-size: 2.5rem;
  margin-bottom: 2rem;
  color: #1a1a1a;
}

.info-content p {
  color: #666;
  line-height: 1.8;
  font-size: 1.1rem;
}

.info-image {
  height: 400px;
  background: url('https://images.unsplash.com/photo-1590644365607-1c5a0c72c150?ixlib=rb-4.0.3') center/cover;
  border-radius: 10px;
}

.values {
  background-color: #f8f9fa;
  padding: 5rem 2rem;
}

.values h2 {
  text-align: center;
  font-size: 2.5rem;
  margin-bottom: 3rem;
  color: #1a1a1a;
}

.values-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 2rem;
  max-width: 1200px;
  margin: 0 auto;
}

.value-card {
  background-color: white;
  padding: 2rem;
  border-radius: 10px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  text-align: center;
  transition: transform 0.3s ease;
}

.value-card:hover {
  transform: translateY(-5px);
}

.value-card h3 {
  font-size: 1.5rem;
  margin-bottom: 1rem;
  color: #1a1a1a;
}

.value-card p {
  color: #666;
  line-height: 1.6;
}

.team {
  padding: 5rem 2rem;
}

.team h2 {
  text-align: center;
  font-size: 2.5rem;
  margin-bottom: 3rem;
  color: #1a1a1a;
}

.team-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 2rem;
  max-width: 1200px;
  margin: 0 auto;
}

.team-member {
  text-align: center;
  text-decoration: none;
  display: block;
  padding: 1.5rem;
  border-radius: 10px;
  transition: all 0.3s ease;
  background-color: white;
  box-shadow: 0 4px 6px rgba(86, 88, 89, 0.1);
}

.team-member:hover {
  transform: translateY(-5px);
  box-shadow: 0 6px 12px rgba(86, 88, 89, 0.15);
}

.member-image {
  width: 200px;
  height: 200px;
  border-radius: 50%;
  margin: 0 auto 1.5rem;
  background: url('https://images.unsplash.com/photo-1590644365607-1c5a0c72c150?ixlib=rb-4.0.3') center/cover;
}

.team-member h3 {
  font-size: 1.5rem;
  margin-bottom: 0.5rem;
  color: #565859;
}

.team-member p {
  color: #6E7476;
}

@media (max-width: 768px) {
  .company-info {
    grid-template-columns: 1fr;
  }

  .info-content h2 {
    font-size: 2rem;
  }
} 